.loading_bg {
  width: 100px;
  height: 100px;
  background-color: #000;
  opacity: 0.7;
  border-radius: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: fixed;
  top: 40%;
  left: 40%;
  z-index: 999999;
}
.loading_icon {
  width: 40px;
  height: 40px;
}
.loading_content {
  padding-top: 10px;
