}
* {
  padding: 0;
  margin: 0;
  -webkit-user-select: none;
  /*禁止用户选中文字*/
  -webkit-touch-callout: none;
  /*禁止ios长按时触发系统的菜单,禁止ios&android长按时下载图片*/
}
form {
  -webkit-appearance: none;
  /*webkit去除表单元素的默认样式*/
}
li {
  list-style: none;
}
img {
  border: 0;
  vertical-align: middle;
}
input, textarea {
  border: none;
  outline: none;
  cursor: pointer;
  font-size: .48rem;
}
textarea {
  resize: none;
}
a, input::-webkit-input-placeholder {
  color: rgba(26, 27, 57, 0.3);
  font-size: .48rem;
}
body {
  -webkit-text-size-adjust: 100% !important;
  /*ios使用-webkit-text-size-adjust禁止调整字体大小*/
  font-family: Helvetica;
}
a, button, input {
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
  /*去掉高亮*/
}
input::-webkit-input-speech-button {
  display: none;
}
/*android 上去掉语音输入按钮*/
/*  去掉按钮默认样式开始  */
button {
  border: none;
  background-color: transparent;
  outline: none;
}
/*  去掉按钮默认样式结束  */
a {
  text-decoration: none;
}
/*  清除浮动开始  */
.clear:after {
  content: "";
  display: block;
  clear: both;
}
.clear {
  zoom: 1;
  /*用css中的zoom属性可以让网页实现IE7中的放大缩小功能。 zoom：1就是和原来一样大小*/
}
/*  清除浮动开始结束  */
/*  获取验证码开始  */
.inpt input.veri_input {
  width: 65% !important;
  border-right: 1px solid rgba(26, 27, 57, 0.2);
}
.inpt .veri_btn {
  color: #4cc3ad;
  font-size: .48rem;
  border: none;
  background: none;
  outline: none;
}
.margin-top-20 {
  margin-top: .2rem;
}
/*  获取验证码结束  */
/*  保存按钮开始  */
.preserve_btn {
  display: block;
  width: 100%;
  line-height: 1.25rem;
  font-size: .48rem;
  color: white;
  text-align: center;
  background-color: #5FCCC6;
  position: fixed;
  bottom: 0;
}
/*  保存按钮结束 */
/*  居中(返回首页)按钮开始 */
.middle_btn {
  display: block;
  width: 4.54rem;
  padding: .39rem 1.3rem;
  text-align: center;
  border: 1px solid #4cc3ad;
  border-radius: 7px;
  font-size: .46rem;
  color: #4cc3ad;
  margin: 0 auto;
}
/*  居中（返回首页）按钮结束 */
/*  添加商品按钮开始  */
.add_goods_btn {
  background-color: #4CC3AD;
  color: white;
}
/*  添加商品按钮结束  */
/*  带阴影的按钮开始  */
.img_btn {
  display: block;
  padding: 0 1.7rem;
  position: relative;
}
.img_btn span {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 1.7rem;
  right: 1.7rem;
  line-height: 1.15rem;
  font-size: .48rem;
  color: #ffffff;
  text-align: center;
}
.img_btn img {
  width: 100%;
}
/*  带阴影的按钮结束  */
